import '../static/css/wetherForecast.css';
import uniDrawer from '@/components/common/uni-drawer';
import chart_WeatherLine from '@/components/general/chart_WeatherLine.vue'
export default {
	data() {
		return {
			programGengduoBtn: true,
			currentCity: '成都',
			currentType: '晴转多云',
			currentIcon: 'icon-duoyun',
			wetherList: [
				{
					date: '05-17',
					xq: '周五',
					icon: 'icon-xiaoxue',
					type: '晴',
					wd: '18~25℃',
				  }, {
					date: '今天',
					xq: '周六',
					icon: 'icon-xiaoxue',
					type: '晴',
					wd: '18~25℃'
				  }, {
					date: '05-19',
					xq: '周日',
					icon: 'icon-xiaoxue',
					type: '晴',
					wd: '18~25℃'
				  }, {
					date: '05-20',
					xq: '周天',
					icon: 'icon-xiaoxue',
					type: '晴',
					wd: '18~25℃'
				  }, {
					date: '05-21',
					xq: '周一',
					icon: 'icon-xiaoxue',
					type: '晴',
					wd: '18~25℃'
				  }
			],
			cityList: [
				{
					cityName: '成都',
					wd: '10~20℃'
				  }, {
					cityName: '成都',
					wd: '10~20℃'
				  }
			],
			showRigth: false,
		}
	},
	onNavigationBarButtonTap(btn){
		console.log(JSON.stringify(btn))
		this.gengduoFun();
	},
	onLoad(options) {
		//动态设置标题
		if (options.label) {
		  uni.setNavigationBarTitle({
			title: options.label
		  })
		}
		uni.setNavigationBarColor({
		  frontColor: '#ffffff',
		  backgroundColor: '#48c6ef',
		  animation: {
			duration: 400,
			timingFunc: 'easeIn'
		  }
		})
		//获取当前位置
		uni.getLocation({
			type: 'wgs84',//默认为 wgs84 返回 gps 坐标，gcj02 返回国测局坐标
			success: function (res) {
				console.log('当前位置的经度：' + res.longitude);
				console.log('当前位置的纬度：' + res.latitude);
			},
			fail: function (err) {
				console.log(err);
			}
		});
		uni.getSystemInfo({
			success: (res) => {
				//安卓、ios app和小程序因为右侧模块分布不同，因此更多的图标展示位置不同
				if(res.platform === "android" || res.platform === "ios"){
					this.programGengduoBtn = false;
				}else{
					this.programGengduoBtn = true;
				}
			}
		})
	},
	methods:{
		gengduoFun(){
			this.showRigth = true
		},
		closeDrawer() {
			this.showRigth = false
		},
		hide() {
			this.showRigth = false
		},
		citySelector(){
			uni.navigateTo({
				url: '/views/citySelector',
			})
		}
	},
	components:{
		uniDrawer,
		chart_WeatherLine
	}
}
